body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Pixels";
  src: local("Pixels"),
  url(/static/media/pixel.5fa66681.ttf) format("truetype");
  font-weight: bold;
}
/* Customize your main colors in :root variables */
:root {
  --main-background-color: #6ca042;
  --card-background-color: #bac76b;
  --countdown-background-color: #84b75a;
  --main-text-color:#F7F6F4;
  --title-text-color:#3CBA8B;
}

body{
background-color: #bac76b;
  font-family: 'Patrick Hand', cursive;
  font-size: 1.1em;
}

h2, p{
  color: #F7F6F4;
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}




.hovered-div {
    transition: all 0.3s;
    padding-bottom: 8px;
    padding-top: 8px;
    cursor: pointer;
}


.hovered-div:hover {
    background-color: var(--card-background-color);

}

.countDown-container {
    display: flex;
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    flex-direction: row;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    color: white;
    font-size: 18px;
}

.item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 20%;
    background-color: var(--countdown-background-color);
}

.countDown-text {
    color: #fff;
    font-weight: bold;
}

.glow-count-text {
    text-transform: uppercase;
    -webkit-animation: glow 2s ease-in-out infinite alternate;
            animation: glow 2s ease-in-out infinite alternate;
    color: var(--main-text-color);
    font-family: Pixels }
@-webkit-keyframes glow {
    from {
        text-shadow: 0 0 20px var(--main-text-color);
    }
    to {
        text-shadow: 0 0 30px var(--title-text-color), 0 0 10px var(--title-text-color);
    }
}
@keyframes glow {
    from {
        text-shadow: 0 0 20px var(--main-text-color);
    }
    to {
        text-shadow: 0 0 30px var(--title-text-color), 0 0 10px var(--title-text-color);
    }
}

.image-container {
    position: relative;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    min-width: 300px;
    width: 100%;
}

.first-image {
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
    min-width: 300px;
    width: 100%;
    border-radius: 10px;
}

.second-image {
    z-index: 2;
    position: absolute;
    top: -8px;
    left: 0;
    min-width: 300px;
    width: 100%;
}
